<template><section><h1>tag</h1>
<template>
    <coding title="普通的" content="有5种样式" :code="data1">
        <y-tag type="info">info</y-tag>
        <y-tag type="success">success</y-tag>
        <y-tag type="warning">warning</y-tag>
        <y-tag type="danger">danger</y-tag>
        <y-tag type="gray">gray</y-tag>
    </coding>
    <coding title="关闭按钮" content="加上closable属性" :code="data2">
        <y-tag type="info" closable="">info</y-tag>
        <y-tag type="success" closable="">success</y-tag>
        <y-tag type="warning" closable="">warning</y-tag>
        <y-tag type="danger" closable="">danger</y-tag>
        <y-tag type="gray" closable="">gray</y-tag>
    </coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>样式</td>
<td><code>String</code></td>
<td>-</td>
<td><code>info</code>, <code>success</code>,<code>warning</code>,<code>danger</code>,<code>gray</code></td>
</tr>
<tr>
<td>closable</td>
<td>删除按钮</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
data1:
`<y-tag type="info">info</y-tag>
<y-tag type="success">success</y-tag>
<y-tag type="warning">warning</y-tag>
<y-tag type="danger">danger</y-tag>
<y-tag type="gray">gray</y-tag>`,
data2:
`<y-tag type="info" closable>info</y-tag>
<y-tag type="success" closable>success</y-tag>
<y-tag type="warning" closable>warning</y-tag>
<y-tag type="danger" closable>danger</y-tag>
<y-tag type="gray" closable>gray</y-tag>`,
        }
    }
}
</script>


